label {
  display: block;
  margin-top: 10px;
  font-weight: bold;
}
.signup {
  background: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRbJhtYwjERdYwkkFeVaiT69AcKxHyZ_hEGtQ&usqp=CAU");
