.navbar {
  background: url("https://media.istockphoto.com/vectors/grey-and-white-tech-arrows-abstract-background-vector-id976579550?k=6&m=976579550&s=612x612&w=0&h=aBiXw_QxbwySBIHusVlowfr46amKzHicoBotEKEOxN0=");
